.login {

    &__container {
        display: flex;
        align-items: center;
        margin-left: 100px;
    }

    &__left {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;
        margin-right: 50px;
        margin-left: 50px;
    }

    &__input {
        background: #FFFFFF;
        border: 1px solid #C7CCD0;
        border-radius: 8px;
        padding-left: 16px;
        padding-top: 11px;
        padding-bottom: 11px;
        width: 361px;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
    }

    &__btn {
        border: none;
        outline: none;
        background: linear-gradient(180deg, #E93B77 0%, #DA1F63 100%);
        border-radius: 8px;
        font-style: normal;
        font-weight: 700;
        font-size: 12px;
        padding-top: 15px;
        padding-bottom: 15px;
        line-height: 140%;
        width: 381px;
        text-align: center;
        color: #FFFFFF;
    }
    &__right {

    }
    &__img {
        width: 100vh;
    }
}

.container {
    max-width: 1240px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
}