.modal {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;


    &__inner {
        width: 500px;
        background-color: #fff;
        padding: 20px;
    }

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
    }

    &__btn {
        border: none;
        outline: none;
        background: linear-gradient(180deg, #E93B77 0%, #DA1F63 100%);
        color: white;
        margin-left: 350px;
    }
}